:root{
    --colorFondo: #616668;
    --colorFondoBoard: #E7E7E7;    
    --colorBordeBoard: #ffffff;
    --colorFondoPanel: #E7E7E7;
    --colorBordePanel: #ffffff;
    --colorFondoButton: #C6C6C6;
    --colorFondoBtnHover: #A9A9A9;
    --player-no-player: white;
    --player-player1: #74ab90;
    --player-player2: #ffb145;
}

body{
    background-color: var(--colorFondo);
    font-family: "Franklin Gothic Pedium", "Arial Narrow", Arial, sans-serif;        
    
    
}

.main{    
    width: 1200px;
    height: 900px;
    margin: 40px auto;
    position: relative;
}

.app-container{
    width: 800px;
    height: 800px;    
    position: absolute;
    left: 50%;
    top: 10%;
    margin-left: -400px;   
    
}


.panel{
    position: absolute;
    background-color: var(--colorFondoPanel);
    border: 8px solid var(--colorBordePanel);
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    z-index: 1;    
}


.header{
    left: 50%;
    top: 20px;
    height: 80px;
    width: 300px;
    margin-left: -160px;
    position: absolute;
}

.header-text{
    font-size: 30px;    
    font-weight: bold;
    width: 200px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.footer{
    left: 50%;
    top: 75%;
    height: 80px;
    width: 310px;
    margin-left: -160px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    
}

.footer button{
    position: relative;
    left: 50%;
    top: 20%;
    width: 160px;
    height: 50px;    
    background-color: var(--colorFondoButton);
    font-size: 20px;
    -webkit-box-shadow: 3px 5px 10px 2px rgba(0, 0, 0, 0.9);
    box-shadow: 3px 5px 10px 2px rgba(0, 0, 0, 0.9);
    border: 5px solid var(--colorBordePanel);
    font-weight: bold;
}

.footer button:hover{
    background-color: var(--colorFondoBtnHover);    
    cursor: pointer;    
}

.gameBoard{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);    
    padding: 20px;
    width: 520px;
    position: absolute;
    left: 50%;
    margin-left: -300px;
    top: 10%;
    background-color: var(--colorFondoBoard);
    border: 8px solid var(--colorBordeBoard);
    border-radius: 8%;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    box-shadow: 7px 5px 15px 3px rgba(0, 0, 0, 0.9);
    
}

.gameCircle{
    width: 100px;
    height: 100px;
    margin: 10px auto;
    border-radius: 50%;
    border: 5px solid black;
}

.player_0{
    background-color: var(--player-no-player);
}


.player_1{
    background-color: var(--player-player1);
}

.player_2{
    background-color: var(--player-player2);
}

@media screen and (max-width: 1000px){
    .main{    
        width: 900px;
        height: 900px;
        margin: 40px auto;
        position: relative;       
    }

     .gameBoard{
        width: 420px;
        height: 420px;
        left: 60%;
        top: 20%;
        position: absolute;
     }   

     .gameCircle{
        width: 70px;
        height: 70px;
        margin: 10px auto;
     }

     .header{
        top: 10%;
        left: 52%;
        position: absolute;
     }

     .footer{
        position: absolute;
        top: 75%;
        left: 53%;
     }     
}


@media screen and (max-width: 760px){
    .main{    
        width: 600px;
        height: 750px;
        margin: 100px auto;
        position: relative;        
    }

     .gameBoard{
        width: 420px;
        height: 420px;
        left: 60%;
        top: 20%;
        position: absolute;
     }   

     .gameCircle{
        width: 70px;
        height: 70px;
        margin: 10px auto;
     }

     .header{
        top: 7%;
        left: 52%;
        position: absolute;
     }

     .footer{
        position: absolute;
        top: 85%;
        left: 53%;
     }
}